.TEI {
    background-color:  white;
    position: relative;
    
  }

.TEI_paper {
  margin-top: 10px;
  padding: 50px;
  font-family: "Lora", "Serif";
}

  tei-head {
    font-size: 180%;
  }

  
  tei-persname:hover, tei-placename:hover{
    cursor: pointer;
    text-decoration: underline;
  }
  
  .TEI tei-back {
    display:none;
  }
  
  tei-div.active {
    background-color: LightYellow;
  }
  
  .chapter-view tei-TEI tei-label {
    display: block;
    font-family: Arvo, sans-serif;
    font-weight: normal;
    font-size: 150%;
    margin-top: .5rem;
  }
  
  .chiastic-view .Parallel:before { 
    display: block;
    font-family: Arvo, sans-serif;
    font-weight: normal;
    font-size: 180%;
    margin-top: .5rem;
    content: attr(display); 
  }

  .chiastic-view .Parallel  {
    display: block;
  }
  
  .chiastic-view .Parallel .Parallel:before { 
    font-size: 150%;
  }
  
  .chiastic-view .Parallel .Parallel {
    margin-left: 3rem;
  }
  
  .chiastic-view tei-TEI tei-label {
    font-style: italic;
    display: block;
  }

  .TEI .MuiAppBar-root {
    margin: 0 -20px 0 -20px;
    width: calc(100% + 40px);
}